// import * as React from 'react';
import { connect } from 'react-redux';

import { IAppState } from '../../store/Store';
import { IQuestion, IQuestionAnswer, IQuestionGroup } from '../types'
import { IAnswer } from '../../Answers/types'

import { Dispatch } from 'redux';  // ActionCreatorsMapObject, 

import { QuestionActions,  
	getQuestion, 
	addQuestion, 
	editQuestion,
	removeQuestion,
	storeQuestion,
	cancelQuestion,
	addGroup,
	editGroup,
	removeGroup,
	storeGroup,
	removeQuestionAnswer,
	assignQuestionAnswer,
	setIsDetail,
	addAndAssignNewAnswer,
	selectQuestionAnswer,
	copyQuestionAnswer,
	toggleGroup
} from '../actions'

import QuestionsPage from '../components/QuestionsPage'
import SupportPage from '../components/SuportPage'

const joinQuestionAnswers = (question: IQuestion | undefined, answers: IAnswer[]) : IQuestionAnswer[]=> {
	if (question === undefined || question.answers.length === 0 || answers === undefined)
		return [];
	console.log({question, answers})
	const questionAnswers = question.answers.map(qa => ({...qa, text: answers.find(a => a.answerId === qa.answerId)!.text}));
	return questionAnswers.sort((a,b) => a.assigned < b.assigned ? 1 : -1);
}

interface IProps {
	canEdit: boolean
}

// Grab the questions from the store and make them available on props
const mapStateToProps = (store: IAppState, ownProps: IProps ) => {
	const {questionsState, answerState, userState} = store;
	const { questionGroups, question, groupOptions, formMode, groupIdEditing, isDetail } = questionsState; 
	const { answers } = answerState;
	return {
		questionGroups,
		groupOptions,
		question: question!,
		questionAnswers: joinQuestionAnswers(question, answers),
		answers,
		formMode,
		groupIdEditing,
		canEdit: ownProps.canEdit,
		isDetail,
		userOptions: userState.users.map(user => ({ label: user.name, value: user.userId }))
	};
};

const mapDispatchToProps = (dispatch: Dispatch<QuestionActions>) => {
	return {
		onSelectQuestion: (questionId: number) => dispatch<any>(getQuestion(questionId)),
		add: (questionGroupId: number, text: string) => dispatch<any>(addQuestion(questionGroupId, text)),
		edit: (questionGroupId: number, questionId: number) => dispatch<any>(editQuestion(questionGroupId, questionId)),
		remove: (questionGroupId: number, questionId: number) => dispatch<any>(removeQuestion(questionGroupId, questionId)),
		saveForm: (question: IQuestion, formMode: string) => dispatch<any>(storeQuestion(question, formMode)),
		cancel: () => dispatch<any>(cancelQuestion()),

		// groups
		addGroup: () => dispatch<any>(addGroup()),
		toggleGroup: (groupId: number) =>  dispatch<any>(toggleGroup(groupId)),
		editGroup: (groupId: number) =>  dispatch<any>(editGroup(groupId)),
		removeGroup: (groupId: number) => dispatch<any>(removeGroup(groupId)),
		storeGroup: (group: IQuestionGroup) => dispatch<any>(storeGroup(group)),

		// question answers
		selectQuestionAnswer: (groupId: number, questionId: number, answerId: number) => 
			dispatch<any>(selectQuestionAnswer(groupId, questionId, answerId)),
		copyQuestionAnswer: (groupId: number, questionId: number, answerId: number) => 
			dispatch<any>(copyQuestionAnswer(groupId, questionId, answerId)),
		removeQuestionAnswer: (groupId: number, questionId: number, answerId: number) => 
			dispatch<any>(removeQuestionAnswer(groupId, questionId, answerId)),

		assignQuestionAnswer: (groupId: number, questionId: number, answerId: number) => 
			dispatch<any>(assignQuestionAnswer(groupId, questionId, answerId)),
		
		setIsDetail: (isDetail: boolean) => {
			dispatch<any>(setIsDetail(isDetail))
		},

		addAndAssignNewAnswer: (groupId: number, questionId: number, answer: IAnswer, formMode: string) => {
			dispatch<any>(addAndAssignNewAnswer(groupId, questionId, answer, formMode))
		}
	}
}


export default {
	questions: connect(mapStateToProps, mapDispatchToProps)(QuestionsPage),
	promoter: connect(mapStateToProps, mapDispatchToProps)(SupportPage)
};