// Import Reducer type
import { Reducer } from 'redux';

import {
	QuestionActions,
	QuestionActionTypes
} from './actions';

import { IQuestion, IQuestionGroup, IQuestionsState } from './types'

export const SUPPORT_QUESTIONS = 'SUPPORT_QUESTIONS';

export const initialQuestion: IQuestion = {
	groupId: 0,
	questionId: 0,
	text: '',
	words: [],
	source: 0,
	status: 0,
	answers: [],
	createdBy: 0,
	created: new Date()
};

export const initialQuestionGroup: IQuestionGroup = {
	groupId: 0,
	title: '',
	questions: [],
	isExpanded: false,
	createdBy: 1,
	created: new Date()
};


// Define the initial state
export const initialQuestionsState: IQuestionsState = {
	questionGroups: [],
	question: undefined,
	groupOptions: [],
	loading: false,
	formMode: 'display',
	groupIdEditing: 0,
	isDetail: false
};


