import React from 'react';
import * as ReactDOM from 'react-dom';

import { HashRouter as Router, Route, Switch, Link } from 'react-router-dom' // useRouteMatch

import { Provider } from 'react-redux';

import { Store } from 'redux';

import configureStore, { IAppState } from './store/Store';
import { getAllQuestions } from './Questions/actions';
import { getAllAnswers } from './Answers/actions';

import './index.css';
import './App.css';

import App from './components/App';
import AnswersPage from './Answers/containers/Page'
import containers from './Questions/containers/Pages'

import './formik/formikStyles.css';
import { coolColors } from './cool-colors'
import { getAllUsers } from './user/actions';

interface IProps {
	store: Store<IAppState>;
}

// <Router basename={'/'}>
const Root: React.SFC<IProps> = props => {
	return (
		<Provider store={props.store} >
			<Router basename={'/'}>
				<nav>
					<ul>
						<li>
							<Link to="/supporter/promo">Supporter</Link>
						</li>
						<li>
							<Link to="/questions">Questions</Link>
						</li>
						<li>
							<Link to="/answers/pera">Answers</Link>
						</li>
						<li  className="push-right">
							<Link to="/answers/sima">Sign In</Link>
						</li>
					</ul>					
				</nav>
				<div>
					<Switch>
						{/* exact */}
						<Route exact path="/supporter/:tekst?">
							{<App />}
						</Route>
						<Route path="/questions">
							<containers.questions canEdit={true} />
						</Route>
						<Route path="/answers/:slug">
							<AnswersPage />
						</Route>
						{/* <Route
							path="/blog2/:slug"
							render={({ match }) => {
								// Do whatever you want with the match...
								return <div>{match}</div>;
							}}
						/> */}
					</Switch>
				</div>	
			</Router>
		</Provider>
	);
};

coolColors();

// Generate the store
localStorage.clear(); // !!!!!!!!!!!!

const store = configureStore();
store.dispatch(getAllQuestions());
store.dispatch(getAllAnswers());
store.dispatch(getAllUsers())

// Render the App
// ReactDOM.render(<Root store={store} />, document.getElementById(
//   'root'
// ) as HTMLElement);

// React.StrictMode
ReactDOM.render(
	<React.StrictMode>
	  <Root store={store} />
	</React.StrictMode>,
	document.getElementById('root') 
  );