import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup'
import { COLORS } from '../../formik/theme';
import { IOption, Select } from '../../common/Select'
import { IQuestion, IQuestionAnswer } from '../types';
import { IAnswer } from '../../Answers/types';

import QuestionAnswers from './QuestionAnswers'

interface IProps {
  question: IQuestion;
  questionAnswers: IQuestionAnswer[];
  answers: IAnswer[];
  formMode: string;
  canEdit: boolean,
  edit: () => void;
  remove: () => void;
  selectQuestionAnswer: (groupId: number, questionId: number, answerId: number) => void;
  copyQuestionAnswer: (groupId: number, questionId: number, answerId: number) => void;
  removeQuestionAnswer: (groupId: number, questionId: number, answerId: number) => void;
  assignQuestionAnswer: (groupId: number, questionId: number, answerId: number) => void;
  setIsDetail: (isDetail: boolean) => void;
  userOptions: IOption<number>[];
  sourceOptions: IOption<number>[];
  statusOptions: IOption<number>[];
  groupOptions: IOption<number>[];
}

const Form: React.FC<IProps> = (props: IProps) => {
  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      groupId: props.question.groupId,
      questionId: props.question.questionId,
      text: props.question.text,
      source: props.question.source,
      status: props.question.status,
      answers: props.question.answers,
      createdBy: props.question.createdBy,
      created: props.question.created,
      userOptions: props.userOptions,
      sourceOptions: props.sourceOptions,
      statusOptions: props.statusOptions
    },
    validationSchema: Yup.object({
      text: Yup.string()
        .max(150, 'Must be 150 characters or less')
        .required('Required'),
      /*answers: Yup.string()
        .max(20, 'Must be 20 characters or less')
        .required('Required'),
      email: Yup.string()
        .email('Invalid email address')
        .required('Required'),*/
    }),
    onSubmit: values => {
      // alert(JSON.stringify(values, null, 2));
    },
  });

  return (
    <form onSubmit={formik.handleSubmit}>

      <label className="id" htmlFor="questionId">QuestionId:</label>
      {/* <input
        id="questionId"
        name="questionId"
        type="text"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.questionId}
        disabled
        style={{ width: '50px' }}
      />
      {formik.touched.questionId && formik.errors.questionId ? (
        <div>{formik.errors.questionId}</div>
      ) : null} */}
      <span id="questionId">{formik.values.questionId}</span>

      <label htmlFor="text"></label>
      <textarea
        id="text"
        name="text"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.text}
        disabled
        style={{ width: '100%' }}
        rows={2}
      />
      {formik.touched.text && formik.errors.text ? (
        <div>{formik.errors.text}</div>
      ) : null}

      <label htmlFor="createdBy">Source</label>
      <Select
        id="source"
        name="source"
        options={formik.values.sourceOptions}
        onChange={formik.handleChange}
        value={formik.values.source}
        disabled={true}
      />

      <br />
      <QuestionAnswers
        question={props.question}
        questionAnswers={props.questionAnswers}
        canEdit={props.canEdit}
        formMode={props.formMode}
        selectQuestionAnswer={props.selectQuestionAnswer}
        copyQuestionAnswer={props.copyQuestionAnswer}
        removeQuestionAnswer={props.removeQuestionAnswer}
        setIsDetail={props.setIsDetail}
      />
      <br />

      <label htmlFor="status">Status</label>
      <Select
        id="status"
        name="status"
        options={formik.values.statusOptions}
        onChange={formik.handleChange}
        value={formik.values.status}
        disabled={true}
      />

      <label htmlFor="createdBy">Created by</label>
      <Select
        id="createdBy"
        name="createdBy"
        options={formik.values.userOptions}
        onChange={formik.handleChange}
        value={formik.values.createdBy}
        disabled={true}
      // onChange={(gradeId: number) =>
      // 	dispatch({
      // 		type: StudentActionTypes.STUDENT_ASSIGN_GRADE,
      // 		studentGradeIds: {
      // 			studentId: student!.id,
      // 			gradeId: gradeId,
      // 			gradeName: gradesAll[gradeId].name
      // 		}
      // 	})
      // }
      />


      <label className="id" htmlFor="created">Created:</label>
      {/* <input
        id="created"
        name="text"
        type="text"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.created.toLocaleDateString()}
      />
      {formik.touched.created && formik.errors.created ? (
        <div>{formik.errors.created}</div>
      ) : null} */}
			<span>{formik.values.created.toLocaleDateString()}</span>

      {/* 
      <label htmlFor="answers">Answers</label>
      <input
        id="answers"
        name="answers"
        type="text"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.answers}
      />
      {formik.touched.answers && formik.errors.answers ? (
        <div>{formik.errors.answers}</div>
      ) : null}



      <label htmlFor="email">Email Address</label>
      <input
        id="email"
        name="email"
        type="email"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.email}
      />
      {formik.touched.email && formik.errors.email ? (
        <div>{formik.errors.email}</div>
      ) : null} */}

      {/* <button type="submit">Submit</button> */}
      {props.canEdit &&
        <div className="buttons">
          <button onClick={() => props.edit()}>Edit</button>
          <button onClick={() => props.remove()}>Remove</button>
        </div>
      }
    </form>
  );
};


const color = 'blue';

export const DisplayForm: React.FC<IProps> = (props: IProps) => {
  return (
    <div style={{ height: '100%' }} className="formik-example formik-example--blue">
      <div
        style={{
          height: '100%',
          background: COLORS[color][5],
          padding: '1rem 1rem',
        }}
      >
        <div
          style={{
            borderRadius: '4px',
            boxShadow: '0 8px 16px rgba(0,0,0,.2)',
            background: '#fff',
            maxWidth: '90%',
            margin: '0 auto',
            padding: '1rem',
          }}
        >
          <div className="formik-example formik-example--blue">
            <Form {...props} />
          </div>
        </div>
      </div>
    </div>
  )
}