import * as React from 'react';

import { useHover } from '../../common/useHover'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWindowClose, faEdit, faCaretRight, faCaretDown } from '@fortawesome/free-solid-svg-icons'

import { IQuestionGroup } from '../types';


interface IQuestionRowProps {
	questionGroup: IQuestionGroup;
	toggleGroup: (groupId: number) => void;
	editGroup: (groupId: number) => void;
	removeGroup: (groupId: number) => void;
}

const GroupRow: React.FC<IQuestionRowProps> = (props: IQuestionRowProps) => {

	const [hoverRef, hoverProps] = useHover();
	const { questionGroup, toggleGroup, editGroup, removeGroup } = props;
	const {groupId, title, questions, isExpanded} = questionGroup;

	return (
		<div ref={hoverRef} key={groupId} className="name" >
			<button
				className="button-edit"
				title="Expand"
				onClick={() => toggleGroup(groupId)} 
				style={{ marginLeft: '5px' }}
			>
				<FontAwesomeIcon icon={isExpanded?faCaretDown:faCaretRight} color='maroon' size='lg' />
			</button>
			<span className='question-group-title'>{title}</span>
			{hoverProps.isHovered &&
				<button className="button-edit" title="Edit Section" onClick={() => editGroup(groupId)}>
					<FontAwesomeIcon icon={faEdit} color='lightblue' />
				</button>
			}
			{hoverProps.isHovered && questions.length === 0 &&
				<button className="button-remove" title="Remove Section" onClick={() => removeGroup(groupId)}>
					<FontAwesomeIcon icon={faWindowClose} color='lightblue' />
				</button>
			}
		</div>
	)
}

export default GroupRow

