import { applyMiddleware, combineReducers, createStore, Store } from 'redux';
import thunk from 'redux-thunk';

import { questionReducer } from '../Questions/reducer';
import { answerReducer } from '../Answers/reducer';

import { IAnswerState } from '../Answers/types';
import { IQuestionsState } from '../Questions/types';
import { IUserState } from '../user/types';
import { userReducer } from '../user/reducer';

export interface IAppState {
  questionsState: IQuestionsState;
  answerState: IAnswerState;
  userState: IUserState;
}

// Create the root reducer
const rootReducer = combineReducers<IAppState>({
  questionsState: questionReducer,
  answerState: answerReducer,
  userState: userReducer
});

// Create a configure store function of type `IAppState`
export default function configureStore(): Store<IAppState, any> {
  const store = createStore(rootReducer, undefined, applyMiddleware(thunk));
  return store;
}