// Combine the action types with a union (we assume there are more)
export type QuestionActions = IGetAll | IGet | IAdd | IEdit | IRemove | IStore | ICancel |
	IAddGroup | IToggleGroup | IEditGroup | IRemoveGroup | IStoreGroup |
	IRemoveQuestionAnswer | IAssignQuestionAnswer |
	ISetIsDetail | 
	IAddAndAssignNewAnswer;

const isWebStorageSupported = () => 'localStorage' in window

export const addAndAssignNewAnswer: ActionCreator<
	ThunkAction<Promise<any>, IAnswerState, null, IStore>
> = (groupId: number, questionId: number, answer: IAnswer, formMode: string) => {
	return async (dispatch: Dispatch) => {
		try {
			dispatch<any>(addAnswer());
			dispatch<any>(storeAnswer(answer, formMode))
				.then((answerId: number) => {
					console.log('answerId', answerId)
					dispatch<any>(assignQuestionAnswer(groupId, questionId, answerId))
				});
			
		} 
		catch (err) {
			console.error(err);
		}
	};
};


// Get All Action <Promise<Return Type>, State Interface, Type of Param, Type of Action>
export const getAllQuestions: ActionCreator<
	ThunkAction<Promise<any>, IQuestionsState, null, IGetAll>
> = () => {
	return async (dispatch: Dispatch) => {
		try {
			let questionGroups: IQuestionGroup[] = [];
			if (isWebStorageSupported()) {
				const sQuestions = localStorage.getItem(SUPPORT_QUESTIONS);
				console.log('sQuestions', sQuestions)
				if (sQuestions !== null) {
					const groups = JSON.parse(sQuestions);
					questionGroups = parseFromLocalStorage(groups);
				}
				else {
					questionGroups = parseFromJson();
				}
			}
			else {
				questionGroups = parseFromJson();
			}
			dispatch({
				type: QuestionActionTypes.GET_ALL_QUESTIONS,
				questionGroups
			});
		}
		catch (err) {
			console.error(err);
		}
	};
};


