import * as React from 'react';
import { useRef } from 'react'

import { IComponentProps } from '../types'

import { IQuestion } from '../types';

import { AutoSuggest } from '../../components/AutoSuggest';
import { QuestionForm } from './Form'
import { DisplayForm } from './DisplayForm'
import QuestionRow from './QuestionRow';
import GroupRow from './GroupRow';
import { useParams } from 'react-router-dom' // useRouteMatch
import { DetailView } from './DetailView';

import { sourceOptions } from './../../issueSource/sourceOptions'
import { statusOptions } from './../../issueStatus/statusOptions'
import { COLORS } from '../../formik/theme';
const color = 'blue';

type SupportParams = {
	tekst: string;
};

const QuestionsPage: React.FC<IComponentProps> = (props: IComponentProps) => {

	let { tekst } = useParams<SupportParams>();
	const { questionGroups, question, questionAnswers, answers, 
			formMode, groupIdEditing, onSelectQuestion, add, edit, remove, cancel, saveForm, canEdit,
			addGroup, toggleGroup, editGroup, removeGroup, storeGroup,
			selectQuestionAnswer, copyQuestionAnswer, removeQuestionAnswer,
			assignQuestionAnswer,
			setIsDetail, isDetail, addAndAssignNewAnswer,
			userOptions,
			groupOptions } = props;

	const inputEl = useRef<HTMLInputElement>(null);
	setTimeout(() => { 
		if (inputEl.current !== null)	 {
			inputEl.current!.select();
			inputEl.current!.focus()
		}
	}, 100)

	// const expandGroup = (groupId: number): void => {
	// 	const div = document.querySelector<HTMLDivElement>("#divGroup" + groupId);
	// 	console.log(div)
	// 	div!.style.display = 'block';
	// }

	return (
	   <>
			<div className="name-container">
				<div className="two-columns">
					<div className="a">
						<AutoSuggest 
							questionGroups={questionGroups}
							tekst={tekst}
							onSelectQuestion={onSelectQuestion}
						/>
					</div>
					<div className="b">
					</div>
				</div>

				<hr />

				<h4 style={{textAlign: 'center'}}>Maintenance (visible only for Admins) </h4>
				<div className="two-columns">
					<div className="a">
						<h3>All Questions by Groups</h3>
						{questionGroups && 
							questionGroups.map(questionGroup => {
								const {groupId, title, isExpanded, questions} = questionGroup;
								return (
									<div key={groupId} style={{ paddingBottom: '5px'}}>
										<div>
											{groupIdEditing === groupId && 
												<input ref={inputEl} name="groupTitle" type="text" 
													onBlur={(e) => storeGroup({...questionGroup, title: e.target.value})}
													defaultValue={title}
												/>
											}
											{groupIdEditing !== groupId && (
												<GroupRow 
													questionGroup={questionGroup}
													toggleGroup={toggleGroup}
													editGroup={editGroup}
													removeGroup={removeGroup}
												/>
											)}
										</div>
										{ isExpanded &&
											<div className="group-questions">
												{questions.map(question => 
													<QuestionRow
														key={question.questionId}
														question={question}
														onSelectQuestion={onSelectQuestion}
														edit={edit}
														remove={remove}
													/>
												)}
												<div style={{marginLeft: '5%'}}>
													<button className="button-add" title="Add a new Question" onClick={() => add(questionGroup.groupId, '')}>
														Add a new Question
													</button>
												</div>
											</div>
										}
									</div>
								);
						})}
						<div style={{ marginLeft: '1%' }}>
							<button className="button-add-group" title="Add a new Section" onClick={() => addGroup()}>
								Add a new Group
							</button>
						</div>
					</div>
					<div className="b">
						{questionGroups && question &&
							<div style={{border: '1px solid silver', borderRadius: '5px', padding: '5px 5px 15px 5px', background: COLORS[color][5]}}>
								<h4 style={{marginTop: 0, color: 'white'}}>Question</h4>
								{ formMode === 'display' ?
									<DisplayForm
										question={question}
										questionAnswers={questionAnswers}
										answers={answers}
										formMode={formMode}
										canEdit={canEdit}
										edit={() => edit(question.groupId, question.questionId)}
										remove={() => remove(question.groupId, question.questionId)}
										selectQuestionAnswer={selectQuestionAnswer}
										copyQuestionAnswer={copyQuestionAnswer}
										removeQuestionAnswer={removeQuestionAnswer}
										assignQuestionAnswer={assignQuestionAnswer}
										setIsDetail={setIsDetail}
										userOptions={userOptions}
										sourceOptions={sourceOptions}
										statusOptions={statusOptions}
										groupOptions={groupOptions}	
									/>
									:
									<QuestionForm 
										question={question}
										questionAnswers={questionAnswers}
										answers={answers}
										formMode={formMode}
										cancel={cancel}
										saveForm={(question: IQuestion) => saveForm(question, formMode)}
										canEdit={canEdit}
										selectQuestionAnswer={selectQuestionAnswer}
										copyQuestionAnswer={copyQuestionAnswer}
										removeQuestionAnswer={removeQuestionAnswer}
										assignQuestionAnswer={assignQuestionAnswer}
										setIsDetail={setIsDetail}
										userOptions={userOptions}
										sourceOptions={sourceOptions}
										statusOptions={statusOptions}
										groupOptions={groupOptions}	
									/>
								}

							</div>
						}					
					</div>
				</div>
			</div>
			<DetailView 
				groupId={question ? question!.groupId : 0}
				questionId= {question ? question!.questionId : 0}
				addAndAssignNewAnswer={addAndAssignNewAnswer}
				userOptions={userOptions}
			/>	{/* visible={isDetail} */}
	  </>
   );
  }

export default QuestionsPage

