// Import redux types
import { ActionCreator, Dispatch } from 'redux';
import { ThunkAction } from 'redux-thunk';
// Import User Typing
import { IUser, IUserState } from './types';
import data from "./data.json"
const storageUsersDemo: IUser[] = data;

// Create Action Constants
export enum UserActionTypes {
	GET_ALL_USERS = 'GET_ALL_USERS',
  	GET_USER = 'GET_USER'
}

// Interface for Get All Action Type
export interface IGetAll {
	type: UserActionTypes.GET_ALL_USERS;
	users: IUser[];
 }
 
export interface IGet {
	type: UserActionTypes.GET_USER;
	user: IUser;
}

// localStorage
export const SUPPORT_USERS = 'SUPPORT_USERS' 
const storageUsers: IUser[] = [
]


// Combine the action types with a union (we assume there are more)
export type UserActions = IGetAll | IGet;

const isWebStorageSupported = () => 'localStorage' in window

// Get All Action <Promise<Return Type>, State Interface, Type of Param, Type of Action>
export const getAllUsers: ActionCreator<
  ThunkAction<Promise<any>, IUserState, null, IGetAll>
> = () => {
  return async (dispatch: Dispatch) => {
    try {
		// const response = await axios.get('https://swapi.co/api/people/');
		if (isWebStorageSupported()) {
			const sUsers = localStorage.getItem(SUPPORT_USERS);
			if (sUsers !== null) {
				const Users: IUser[] = JSON.parse(sUsers);
				Users.map(g => storageUsers.push(g))
			}
			else {
				storageUsersDemo.map(g => storageUsers.push(g))	
			}
		}
		else {
			storageUsersDemo.map(g => storageUsers.push(g))
		}

		//const response = await getUsersFromLocalStorage(); 
      dispatch({
        type: UserActionTypes.GET_ALL_USERS,
        users: storageUsers //response.data.results,
      });
    } catch (err) {
      console.error(err);
    }
  };
};



